body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(223, 223, 223);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
* {
  margin: 0%;
  padding: 0%;
  outline: none;
}
.container {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 3px 30px;
}
#title {
  font-size: 1.4rem;
  color: rgb(4, 19, 156);
}
ul {
  list-style: none;
  display: flex;
  align-items: center;
}
li {
  margin: 3px 6px;
  cursor: pointer;
}
#searchBar {
  padding: 2px 4px;
}
/* Alert css */
#alertDiv{
  background-color: aqua;
  padding: 3px 10px;

}
/* stateForm css */

#textbox {
  height: 33vh;
  width: 50vw;
  border: 3px solid goldenrod;
}
.btn {
  padding: 4px 14px;
  margin: 3px 3px;
  color: aliceblue;
  background-color: rgb(58, 58, 250);
  border: none;
  border-radius: 3px;
  cursor: pointer;
}
.btn:hover {
  background-color: rgb(44, 44, 252);
}
